import '../styles/NavBar.css';
import { useEffect, useState} from 'react'
function NavBar({setPage, goToWork, darkMode}) {
  const [isLargeScreen, setIsLargeScreen] = useState(false);
  const [isOpen, setIsOpen] = useState(false);

  // Toggle the dropdown visibility
  const toggleDropdown = () => {
    setIsOpen(!isOpen);
  };

  // Function to check window size
  const checkWindowSize = () => {
    setIsLargeScreen(window.innerWidth >= 640);  // Adjust breakpoint here
  };

  // Hook to check window size on mount and resize
  useEffect(() => {
    checkWindowSize();  // Initial check
    window.addEventListener('resize', checkWindowSize);  // Listen for resize

    // Cleanup listener on component unmount
    return () => {
      window.removeEventListener('resize', checkWindowSize);
    };
  }, []);
    return ((
        <div className='nav-bar'>
          <button className='nav-bar-button' onClick={() => setPage("about")} style={{"fontWeight": "bold"}}>Jason Chen</button>
          {isLargeScreen ? 
          <>
              <button className='nav-bar-button' onClick={() =>setPage("experience")}>Projects</button> 
              <button className='nav-bar-button' onClick={ ()=>{setPage("work"); goToWork();}}>Work</button>
              <button className='nav-bar-button' onClick={()=>setPage("contact")}>Contact Me</button>
          </>
          : <div className="dropdown">
          <img onClick={toggleDropdown} className="dropdown-btn" src={`${process.env.PUBLIC_URL}/menu.png`} style={darkMode ? { filter: 'invert(1)' } : {}} >
          </img>
          {isOpen && (
            <div className={`dropdown-menu-${darkMode ? 'dark' : 'light'}`}>
              <button className='nav-bar-button' onClick={() =>setPage("experience")}>Projects</button> 
              <button className='nav-bar-button' onClick={ ()=>{setPage("work"); goToWork();}}>Work</button>
              <button className='nav-bar-button' onClick={()=>setPage("contact")}>Contact Me</button>
            </div>
          )}
        </div>}
        
      </div>
    ))
}
export default NavBar;